import { z } from 'zod'

// Feature flag schema
const FeatureFlagsSchema = z.object({
  enableAuth: z.boolean(),
  enableStats: z.boolean(),
  enablePro: z.boolean(),
  enableSessions: z.boolean(),
  enableFeedback: z.boolean(),
  enableSquads: z.boolean(),
  showBottomNav: z.boolean(),
  showFAQ: z.boolean(),
  showEnvironmentBanner: z.boolean(),
  environmentBannerText: z.string(),
})

export type FeatureFlags = z.infer<typeof FeatureFlagsSchema>

// Get the base URL for API calls
const getApiBaseUrl = () => {
  // Always use relative path for API calls
  return '/api'
}

// Environment configuration
export const config = {
  environment: import.meta.env.MODE || 'development',
  apiUrl: getApiBaseUrl(),
}

// Production defaults
const productionDefaults: FeatureFlags = {
  enableAuth: true,
  enableStats: true,
  enablePro: true,
  enableSessions: true,
  enableFeedback: true,
  enableSquads: true,
  showBottomNav: false,
  showFAQ: false,
  showEnvironmentBanner: true,
  environmentBannerText: 'Solo is sending soon. Follow @gosolonyc for updates',
}

// Feature flag service
export const FeatureFlagService = {
  async initialize(): Promise<FeatureFlags> {
    try {
      console.log('Initializing feature flags from:', `${config.apiUrl}/feature-flags`)
      const response = await fetch(`${config.apiUrl}/feature-flags`)

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }

      const data = await response.json()
      console.log('Received feature flags:', data)

      // Validate the response data
      const flags = FeatureFlagsSchema.parse(data)

      // Force production settings in production environment
      if (config.environment === 'production') {
        flags.showBottomNav = false
        flags.showFAQ = false
      }

      return flags
    } catch (error) {
      console.error('Error fetching feature flags:', error)
      return productionDefaults
    }
  },
}

export { FeatureFlagsSchema, productionDefaults }